import {mapState,mapMutations} from 'vuex'
export default{
	data(){
		return{
			arrays: [],
			index:0,
			sessionId:'',
			myData:[],
			id:1
		}
	},
	onLoad() {
		this.init()
	},
	computed: mapState(['hasLogin','uerInfo']), 
	methods:{
		...mapMutations(['login']),
		// 获取sessionid
		init(){
			uni.getStorage({
				key: 'userInfo',
				success:(res) => {
					this.login(res.data);
					this.sessionId = res.data.sessionId
					uni.request({
						url:'https://www.nssnail.com/wx/getRedCrossList',
						data:{
							sessionId:this.sessionId
						},
						success: (res) => {
							this.myData = res.data.data
							this.myData.forEach((item)=>{
								this.arrays.push(item.mname)
							})
						}
					})
				} 
			})
		},
		
		// 选择机构
		bindPickerChange: function(e) {
			this.index = e.target.value
			this.id = parseInt(this.index)+1
		},
		
		// 保存,选择机构
		sure(){
			uni.request({
				url:'https://www.nssnail.com/wx/userSelectRedCross',
				method:'POST',
				data:{
					sessionId:this.sessionId,
					id:this.id
				},
				success: (res) => {
					if(res.data.stat == 1){
						uni.showToast({
							title:'选择机构成功',
							icon:'success',
							success:()=>{
								setTimeout(function(){
									uni.switchTab({
										url:'../my/my'
									})
								},2000);
							}
						})
					}else if(res.data.stat == 3){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
					}else if(res.data.stat == 0){
						uni.showToast({
							title: res.data.message,
							icon:'none',
							duration:3000
						})
					}
				}
			})
		}
	}
}
